.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: #c6c9ec;
}

h1 {
  font-size: 2.375rem;
  margin: 0 0 1.875rem;
}

.is-christmas-wrapper,
.countdown-wrapper {
  background: #ebecff;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.1);
  border-radius: 0.275rem;
  padding: 3rem;
}

button {
  background: #7076ef;
  border: 0;
  border-radius: 0.375rem;
  padding: 1rem;
  color: #fff;
  font-weight: 700;
  cursor: pointer;
  transition: all 200ms ease-in-out;
  text-transform: uppercase;
  font-size: 0.875rem;
}

button:hover {
  filter: opacity(0.8);
}